  <div>
    <!-- 進度位置 -->
    <div class="process-item my-5">
      <i class="far fa-circle" v-if="!CA.finish && !CA.ing"></i>
      <i class="fas fa-dot-circle" v-if="!CA.finish && CA.ing"></i>
      <i class="fas fa-check-circle" v-if="CA.finish && !CA.ing"></i>
      <div class="process-item-line line-1"></div>
      <i class="far fa-circle" v-if="!GI.finish && !GI.ing"></i>
      <i class="fas fa-dot-circle" v-if="!GI.finish && GI.ing"></i>
      <i class="fas fa-check-circle" v-if="GI.finish && !GI.ing"></i>
      <div class="process-item-line line-2"></div>
      <i class="far fa-circle" v-if="!UPP.finish && !UPP.ing"></i>
      <i class="fas fa-dot-circle" v-if="!UPP.finish && UPP.ing"></i>
      <i class="fas fa-check-circle" v-if="UPP.finish && !UPP.ing"></i>
    </div>

    <div class="wrap my-5">
      <div class="container">
        <div class="row">
          <div class="col-8 offset-2">
            <section class="CA" v-if="CA.ing">
              <h1 class="text-center">Create Account</h1>
              <p class="subtitle text-center">Glad to see you here!</p>

              <form @submit.prevent="onSubmit">
                <div class="form-group">
                  <label for="CAEmail">Account</label>
                  <div class="input-group">
                    <input type="email" class="form-control" :class="{'invalid': errors.has('email')}" id="CAEmail" name="email" v-validate="'required|email'" aria-describedby="emailHelp" placeholder="example@email.com">
                    <i class="fas fa-exclamation-triangle" v-if="errors.has('email')"></i>
                  </div>
                  <small id="emailHelp" class="form-text text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</small>
                </div>
                <div class="form-group">
                  <label for="CAPassword">Password</label>
                  <div class="input-group">
                    <input type="password" v-validate="'required|min:8'" :class="{'invalid': errors.has('pw_confirm')}" name="pw_confirm" ref="pw_confirm" class="form-control" id="CAPassword" placeholder="●●●●●●●●">
                    <i class="fas fa-exclamation-triangle" v-if="errors.has('pw_confirm')"></i>
                  </div>
                  <small class="form-text text-danger" v-if="errors.has('pw_confirm')">{{ errors.first('pw_confirm') }}</small>
                </div>
                <div class="form-group">
                  <label for="CACP">Comfirm Password</label>
                  <div class="input-group">
                    <input type="password" v-validate="'required|confirmed:pw_confirm'" :class="{'invalid': errors.has('confirm_passward')}" name="confirm_passward" class="form-control" id="CACP" placeholder="●●●●●●●●">
                    <i class="fas fa-exclamation-triangle" v-if="errors.has('confirm_passward')"></i>
                  </div>
                  <small class="form-text text-danger" v-if="errors.has('confirm_passward')">{{ errors.first('confirm_passward') }}</small>
                </div>
                <button type="submit" class="btn btn-custom my-5">SUBMIT & NEXT</button>
              </form>
            </section>
            <section class="GI" v-if="GI.ing">
              <h1 class="text-center">General Infomation</h1>
              <p class="subtitle text-center">Tell us who you are!</p>

              <form @submit.prevent="onSubmit2">
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="name">Name
                      <small>(optional)</small>
                    </label>
                    <input id="name" type="text" class="form-control" placeholder="Example Name">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="phone">Phone*</label>
                    <input id="phone" type="number" name="phone" class="form-control" :class="{'invalid': errors.has('phone')}" v-validate="'required|numeric'" placeholder="0912 345 678">
                    <small class="form-text text-danger" v-if="errors.has('phone')">{{ errors.first('phone') }}</small>
                  </div>
                </div>
                <div class="form-row">
                  <div class="col-12">
                    <label for="Bdate">Birth Date
                      <small>(optional)</small>
                    </label>
                    <input id="Bdate" type="date" class="form-control">
                  </div>
                </div>
                <label for="city" class="mt-3">Address*</label>
                <div class="form-row">
                  <div class="form-group col-md-6">
