                    </select>
                  </div>
                  <div class="form-group col-md-6">
                    <select id="dist" class="form-control" required>
                      <option value="Dist" selected disabled>Dist</option>
                      <option :value=item v-for="(item, index) in country" :key="index">{{ item }}</option>
                    </select>
                  </div>
                  <div class="col-12">
                    <input type="text" class="form-control" name="address" :class="{'invalid': errors.has('address')}" v-validate="'required'" placeholder="Address Detail">
                    <small class="form-text text-danger" v-if="errors.has('address')">請填寫地址</small>
                  </div>
                </div>
                <button type="submit" class="btn btn-custom my-5">SUBMIT & NEXT</button>
              </form>
            </section>
            <section class="UPP" v-if="UPP.ing">
              <h1 class="text-center">Update Profile Picture</h1>
              <p class="subtitle text-center">We wanna know you more!</p>
              <div class="form-upload">
                <i class="fas fa-images fa-3x mr-3"></i>
                <div class="form-upload-info  d-inline-block">
                  <div class="upload-info-title">UPLOAD UP TO 3 PHOTOS</div>
                  <div class="upload-info-subtitle">
                    <small>MAXIMUM SIZE: 150*150px</small>
                  </div>
                </div>
                <input type="file" @change="fileSelected">
              </div>
              <div class="alert alert-warning my-3" role="alert" v-if="errImgMes">
                {{ errImgMes }}
              </div>
              <div class="upload-images container">
                <div class="row">
                  <div class="col-md-4 px-0 my-4 upload-img" v-if="imgUlr[index]" v-for="(item, index) in 3" :key="index">
                    <div class="card h-100 mx-2">
                      <img :src="imgUlr[index]" class="card-img h-100">
                      <div class="img-del" @click="imgUlr.splice(index, 1)">
                        <i class="fas fa-trash-alt fa-2x"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <button type="submit" class="btn btn-custom my-5" @click="UPP.finish=true, UPP.ing=false">SUBMIT & NEXT</button>
            </section>
            <section class="congratulation" v-if="UPP.finish">
              <h1 class="text-center">Congratulations</h1>
              <p class="subtitle text-center">Now you’re one of us! </p>
              <div class="text-center">
                <i class="fas fa-smile fa-10x mt-2" style="color: #BEE1FF"></i>
              </div>
            </section>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Validation',
  data () {
    return {
      CA: {
        finish: false,
        ing: true
      },
      GI: {
        finish: false,
        ing: false
      },
      UPP: {
        finish: false,
        ing: false
      },
      cities: [
        {
          name: '台北市',
          children: ['中正區', '大同區']
        },
        {
          name: '新北市',
          children: ['板橋區', '中和區']
        }
      ],
      city: '',
      imgUlr: [],
      errImgMes: ''
    }
  },
  methods: {
    onSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          // do stuff if is valid.
          this.CA.finish = true
          this.CA.ing = false
          this.GI.ing = true
          document.getElementsByClassName('line-1')[0].setAttribute('class', 'process-item-line-finish')
        } else {
          // if(document.getElementsByClassName('btn-custom-fail')[0]){
          //   return;
          // }
          // document.getElementsByClassName('btn-custom')[0].setAttribute("class", "btn-custom-fail")
        }
      })
    },
    onSubmit2 () {
      this.$validator.validate().then(result => {
        if (result) {
          // do stuff if is valid.
          this.GI.finish = true
          this.GI.ing = false
          this.UPP.ing = true
          document.getElementsByClassName('line-2')[0].setAttribute('class', 'process-item-line-finish')
        } else {
          // if(document.getElementsByClassName('btn-custom-fail')[0]){
          //   return;
          // }
          // document.getElementsByClassName('btn-custom')[0].setAttribute("class", "btn-custom-fail")
        }
      })
    },
    fileSelected (e) {
      if (this.imgUlr.length === 3) {
        this.errImgMes = 'Max 3 photos'
      } else {
        const file = e.target.files.item(0)
        if (!file.type.match(/jpeg|png/)) {
          this.errImgMes = 'Only jpeg & png'
          return false
        }
        if (file.size / 1024 > 800) {
          this.errImgMes = 'File size over 800kb'
          return false
        }
        const reader = new FileReader()
        reader.addEventListener('load', this.imageLoaded)
        reader.readAsDataURL(file)
        this.errImgMes = ''
      }
    },
    imageLoaded (e) {
      this.imgUlr.push(e.target.result)
    }
  },
  computed: {
    country: function () {
      const vm = this
      let select = vm.city
      let temp = vm.cities.filter(v => v.name === select)
      if (temp[0]) {
        return temp[0].children
      } else {
        return false
      }
    }
  }
}
</script>

